<template>
  <div class="collection">
    <!-- 标题 -->
    <van-nav-bar title="车型收藏" :fixed="true" left-text="返回" left-arrow @click-left="onClickLeft" />

    <div class="not" v-if="carList.length==0">
      <van-image width="50" height="50" src="http://res.app.yiche.com/wechatapp/cijijingao@2x.png" />
      <p>暂没收藏任何车型</p>
    </div>

    <div class="carBoxs" v-else>
      <div class="carItem" v-for="(item, index) in carList" :key="index">
        <van-image :src="item.image" width="120" height="80" @click="goCarInfo(item.SerialID)">
          <template v-slot:loading>
            <van-loading type="spinner" size="20" />
          </template>
          <template v-slot:error>暂无图片</template>
        </van-image>
        <div class="info" @click="goCarInfo(item.SerialID)">
          <p>{{item.pinpai}}</p>
          <p>{{item.name}}</p>
          <span>{{item.price}}</span>
        </div>
        <van-icon name="cross" @click="remoteItem(item.SerialID)" />
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      carList: []
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    getCollection() {
      this.carList = JSON.parse(localStorage.getItem("car")) || [];
    },
    remoteItem(id) {
      let bool = this.carList.some((item, i) => {
        if (item.SerialID === id) {
          this.carList.splice(i, 1);
          return true;
        }
      });
      if (bool) {
        localStorage.setItem("car", JSON.stringify(this.carList));
        this.$toast("取消收藏");
      }
    },
    goCarInfo(id){
        this.$router.push({path:"/carinfo",query:{SerialID:id}})
    }
  },
  activated() {
    this.getCollection();
  }
};
</script>
<style lang="scss">
.collection {
  padding: 50px 0;
  .van-nav-bar {
    height: 50px;
    line-height: 50px;
    background: #5f93ff;
    color: #fff;
    .van-nav-bar__left {
      .van-nav-bar__text,
      i {
        color: #fff;
      }
    }
    .van-nav-bar__title {
      color: #fff;
    }
  }
  .not {
    width: 300px;
    height: 150px;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    margin: auto;
    text-align: center;
    p {
      font-size: 17px;
      color: #888;
    }
  }
  .carBoxs {
    .carItem {
      padding: 10px 20px;
      border-bottom: 1px solid #eee;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .info {
        p {
          font-size: 14px;
          width: 105px;
          color: #888;
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
        }
        span {
          font-size: 12px;
          color: red;
        }
      }
      //   .van-icon{
      //       left: 50px;
      //   }
    }
  }
}
</style>